import React, { useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';

const StyledAside = styled.aside`
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 11;
  transition: all 0.35s;
  background-color: white;
  box-sizing: border-box;
  @media (max-width: 768px) {
    width: 100%;
    height: 430px;
    bottom: ${(props) => (props.$open ? '0' : '-430px')};
    left: 0;
    border-radius: 20px 20px 0 0;
  }
  @media not (max-width: 768px) {
    width: 500px;
    height: 100%;
    top: 0;
    left: ${(props) => (props.$open ? '0' : '-500px')};
    font-size: 18px;
  }
`;

const StyledWrap = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
  overflow-y: auto;
  @media (max-width: 768px) {
    padding-top: 30px;
  }
  @media not (max-width: 768px) {
    padding: 70px 30px 0;
  }
`;

const StyledHeader = styled.header`
  margin: 0 30px;
  @media not (max-width: 768px) {
    margin-bottom: 30px;
  }
`;

const StyledFooter = styled.footer`
  width: inherit;
  box-sizing: border-box;
  & > button {
    border-top: 1px solid lightgray;
    width: inherit;
    height: 55px;
    text-align: center;
    &:hover {
      background-color: whitesmoke;
    }
  }
`;

const Aside = ({
  open = false,
  title,
  subtitile,
  closeEvent,
  closeText,
  children,
}) => {
  const { t } = useTranslation();
  useEffect(() => {
    if (open) {
      document.body.style = 'overflow-y: hidden';
      return () => (document.body.style = 'overflow-y: auto');
    }
  }, [open]);

  return (
    <StyledAside $open={open}>
      <StyledWrap>
        {(title || subtitile) && (
          <StyledHeader>
            <h2>{title}</h2>
            <span>{subtitile}</span>
          </StyledHeader>
        )}
        {children}
      </StyledWrap>
      <StyledFooter>
        <button onClick={closeEvent}>{closeText || t('btn_close')}</button>
      </StyledFooter>
    </StyledAside>
  );
};

export default Aside;
