import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import {
  BLACK_LANGUAGES,
  INPUT_ID_MENU,
  KEY_LANG,
  WHITE_LANGUAGES,
} from '@/constant';
import Aside from '@components/aside_template';
import Radio from '@components/radio';

const StyledWrap = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  & span:nth-child(2n + 1) {
    color: gray;
  }
  & > button {
    min-width: 100px;
    align-self: flex-end;
  }
  @media (max-width: 768px) {
    align-items: start;
    padding: 0 20px;
    & > fieldset > legend {
      display: none;
    }
  }
  @media not (max-width: 768px) {
    margin: 0 20px 20px;
    padding: 30px 20px;
    border-radius: 20px;
    border: 1px dotted lightgray;
  }
`;

const LanguageAside = ({ open, closeEvent }) => {
  const { t, i18n } = useTranslation();
  const { language, changeLanguage } = i18n;
  const [selectedLang, setSelectedLang] = useState(language);

  useEffect(() => {
    const menuCheckBox = document.getElementById(INPUT_ID_MENU);
    const func = (event) => {
      if (!event.target.checked) {
        setSelectedLang(language);
      }
    };
    menuCheckBox.addEventListener('change', func);
    return () => {
      menuCheckBox.removeEventListener('change', func);
    };
  }, [language]);

  const onChange = (event) => {
    const { value } = event.target;
    setSelectedLang(value);
  };

  const onApply = () => {
    sessionStorage.setItem(KEY_LANG, selectedLang);
    changeLanguage(selectedLang);
    document.documentElement.lang = selectedLang.split('-')[0];
  };

  return (
    <Aside
      open={open}
      title={t('language_settings')}
      subtitile={t('set_language')}
      closeEvent={closeEvent}
      closeText={t('btn_go_back')}
    >
      <StyledWrap>
        <Radio.Group
          name="radio-language"
          checkedValue={selectedLang}
          onChange={onChange}
          column
          title={t('please_select_language')}
        >
          {[...WHITE_LANGUAGES, ...BLACK_LANGUAGES].map((lang) => {
            const i18nKey = lang.split('-')[0];
            return (
              <Radio.Option key={`option_${lang}`} value={lang}>
                <span>{t(i18nKey)}</span>
                {language !== lang && (
                  <span> : {t(i18nKey, { lng: lang })}</span>
                )}
              </Radio.Option>
            );
          })}
        </Radio.Group>
        <button
          className="apply"
          onClick={onApply}
          disabled={selectedLang === language}
        >
          {t('btn_apply')}
        </button>
      </StyledWrap>
    </Aside>
  );
};

export default LanguageAside;
