import React from 'react';
import './foldable_tree.css';

/**
 * /   
 * @param {Object} props -   
 * @param {Object} props.node -   
 * @param {string} props.node.label -   
 * @param {boolean} [props.node.open=false] -    
 * @param {Array<Object>} [props.node.children=undefind] -   
 * @returns {JSX.Element} -    
 */
const FoldableTree = ({ node }) => {
  const TODAY = new Date().getTime();
  return <SubTree id={`tree_${TODAY}`} {...node} />;
};

/**
 *       
 *
 * @param {Object} node -   
 * @param {boolean} node.id -    id
 * @param {string} node.label -   
 * @param {boolean} [node.open=false] -    
 * @param {Array<Object>} [node.children=undefind] -   
 * @returns {JSX.Element} -    
 */
const SubTree = ({ id, open, label, children }) => {
  return (
    <details open={open}>
      <summary>{`${label}`}</summary>
      <ul>
        {children?.map((node, i) => (
          <li key={`${id}-${i}`}>
            {node.children ? (
              <SubTree id={`${id}-${i}`} {...node} />
            ) : (
              <LeafNode {...node} />
            )}
          </li>
        ))}
      </ul>
    </details>
  );
};

/**
 *  
 *
 * @param {Object} node -   
 * @param {string} node.label -   
 * @returns {string} - <li>   
 */
const LeafNode = (node) => {
  return node.label;
};

export default FoldableTree;
