import React from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { SNS_Img } from '@/components/icons8';
import { INFO_EMAIL } from '@/constant';
import profileImg from '@assets/image/google_profile.jpg';
import veloglogoImg from '@assets/image/velog_logo.svg';
import LanguageIcon from '@assets/svg/icon_language.svg';
import Aside from '@components/aside_template';
import './aside.css';

const StyledHeader = styled.header`
  @media (max-width: 768px) {
    display: none;
  }
  @media not (max-width: 768px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    & > figure {
      margin: 0;
      border-radius: 20px;
      overflow: hidden;
    }
    & > figure > img {
      display: block;
      transition: all 5s ease-out;
      max-width: 120%;
      &:hover {
        transform: scale(1.2);
      }
    }
    & > h3 {
      margin: 0;
      & > figure + h3 > ruby > :where(rp, rt) {
        color: gray;
      }
    }
  }
`;

const StyledAddressInfo = styled.address`
  display: flex;
  flex-flow: column wrap;
  & > a::before {
    content: attr(data-icon);
    font-style: normal;
    margin-right: 0.4rem;
  }
  & > a > span:hover {
    color: var(--point1-color);
    text-decoration-line: underline;
  }
`;

const StyledSNSInfo = styled.address`
  margin-top: 10px;
  display: flex;
  gap: 10px;
  & > a {
    padding: 0;
  }
  & > a > img {
    display: block;
    width: 32px;
    &:hover {
      filter: brightness(0.9);
    }
    &[src$='.svg'] {
      padding: 3px;
    }
    &[alt='github'] {
      background-color: white;
      border-radius: 3px;
    }
  }
`;

const StyledDl = styled.dl`
  display: flex;
  flex-flow: column wrap;
  @media (max-width: 768px) {
    flex: 1;
    padding: 0;
    margin-top: 30px;
    & > dt {
      flex: 1;
      text-align: center;
      & img {
        display: none;
      }
    }
  }
  @media not (max-width: 768px) {
    margin: 0 auto;
    gap: 10px;
    & > dt > :where(a, button) {
      width: 100%;
      display: inline-flex;
      gap: 10px;
      padding: 10px 50px;
      &:hover {
        border-radius: 20px;
        background-color: whitesmoke;
      }
      &[data-icon]:before {
        content: attr(data-icon);
      }
    }
  }
`;

const MainAside = ({ open, closeEvent, openLanguageMenu }) => {
  const { t } = useTranslation();
  const GOOGLE_MAPS_URL = `https://www.google.com/maps/place/${t(
    'location_seoul'
  ).replace(' ', '+')}`;

  return (
    <Aside id="default-aside" open={open} closeEvent={closeEvent}>
      <StyledHeader>
        <figure>
          <img
            src={profileImg}
            onContextMenu={(e) => e.preventDefault()}
            onDragStart={(e) => e.preventDefault()}
          ></img>
        </figure>
        <h3
          dangerouslySetInnerHTML={{
            __html: t('junior_developer', { name: t('lhw') }),
          }}
        />
        <StyledAddressInfo>
          <a data-icon="" title="Email" href={`mailto:${INFO_EMAIL}`}>
            <span>{INFO_EMAIL}</span>
          </a>
          <a data-icon="" title="Google Maps" href={GOOGLE_MAPS_URL}>
            <span>{t('location_seoul')}</span>
          </a>
        </StyledAddressInfo>
        <StyledSNSInfo>
          <a
            href="https://velog.io/@dev-hyewon"
            title={t('go_to', { siteName: 'Velog' })}
          >
            <img src={veloglogoImg} alt="velog"></img>
          </a>
          <a
            href="https://github.com/dev-hyewon"
            title={t('go_to', { siteName: 'Github' })}
          >
            {SNS_Img('github')}
          </a>
        </StyledSNSInfo>
      </StyledHeader>
      <StyledDl>
        <dt>
          <a data-icon="" href="#main-section-intro">
            {t('section_title_introduction')}
          </a>
        </dt>
        <dt>
          <a data-icon="" href="#main-section-skill">
            {t('section_title_tech')}
          </a>
        </dt>
        <dt>
          <a data-icon="" href="#main-section-project">
            {t('section_title_work')}
          </a>
        </dt>
        <dt>
          <a data-icon="" href="#main-section-education">
            {t('section_title_education')}
          </a>
        </dt>
        <dt>
          <button onClick={openLanguageMenu}>
            <img src={LanguageIcon} width="24px" />
            <span>{t('language_settings')}</span>
          </button>
        </dt>
      </StyledDl>
    </Aside>
  );
};

export default MainAside;
