import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { INPUT_ID_MENU } from '@/constant';
import LanguageAside from '@/pages/aside/language_aside';
import MainAside from '@/pages/aside/main_aside';
import './header.css';

const OverlayBackground = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(1px);
`;

const Header = () => {
  const [openMain, setOpenMain] = useState(false);
  const [openLanguage, setOpenLanguage] = useState(false);

  useEffect(() => {
    if (openMain || openLanguage) {
      document.body.style = 'overflow-y: hidden';
      return () => (document.body.style = 'overflow-y: auto');
    }
  }, [openMain, openLanguage]);

  const toggleMainMenu = () => setOpenMain(!openMain);

  const closeMainMenu = () => setOpenMain(false);

  const openLanguageMenu = () => {
    setOpenMain(false);
    setOpenLanguage(true);
  };

  const closeLanguageMenu = () => {
    setOpenMain(true);
    setOpenLanguage(false);
  };

  const closeAsides = () => {
    setOpenMain(false);
    setOpenLanguage(false);
  };

  return (
    <div
      id="header-wrapper"
      onClick={(e) => e.target === e.currentTarget && toggleMainMenu()}
    >
      {(openMain || openLanguage) && (
        <OverlayBackground onClick={closeAsides} />
      )}
      <input
        type="checkbox"
        id={INPUT_ID_MENU}
        checked={openMain}
        onChange={toggleMainMenu}
        style={{ display: 'none' }}
      />
      <label htmlFor={INPUT_ID_MENU}>
        <span></span>
        <span></span>
        <span></span>
      </label>
      <MainAside
        open={openMain}
        closeEvent={closeMainMenu}
        openLanguageMenu={openLanguageMenu}
      />
      <LanguageAside open={openLanguage} closeEvent={closeLanguageMenu} />
    </div>
  );
};

export default Header;
